.App {
  text-align: center;
  padding: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-logotype {
  max-width: 75%;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo:active, .App-logo:hover {
    animation: App-logo-spin infinite 20s linear;
  }
}

button:active {
  animation: App-logo-spin infinite 20s linear;
}

.App-link {
  color: #61dafb;
}

.App-spin-button {
  margin: 2.5vh;
}

.App-loading {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 99999;
  background: black;
  opacity: .8;
}

p {
  font-size: calc(14px + 2vmin);
  margin: 1em 0 0 0;
}


@media screen and (min-width: 768px) {
  .App-text-container {
    display: flex;
    align-items: center;
  }
  
  .App-text-container-item {
    flex-direction: column;
    justify-content: center;
    flex: 1;
    flex-shrink:1;
  }
  p {
    margin: 0;
  }

  .App-spin-button {
    margin: 5vh;
  }
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

p {
  font-family: 'Square Peg', cursive;
}